//final transported
@use "./../globals";
@use "./../units";
@use "./../mixins/clear_left.scss";
@use "./../mixins/responsive.scss";
@use "./../mixins/comma_separated.scss";
@use "./../mixins/link.scss";

.b-catalog_entry-tooltip {
  line-height: units.$line_height;
  position: relative;

  .entry-tooltip_container {
    display: flex;
    gap: 20px;

    .image-link img {
      // have to restrict max-width because it can be orignal image instead of cutted preview
      max-width: 160px;
    }
  }

  .inner {
    flex-grow: 1;

    a.name {
      // +link
      font-size: 13px;
      font-weight: bold;
      line-height: 20px;
      margin-bottom: 5px;
    }

    .job-title {
      font-style: italic;
      line-height: 1;
      margin-bottom: 10px;
    }

    .text {
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 5px;

      .b-prgrph {
        margin-bottom: 2px !important;
      }

      .b-nothing_here {
        margin-left: 0;
      }
    }

    .line {
      font-size: 12px;
      line-height: 23px;

      &.name {
        line-height: 1;
        margin-bottom: 5px;
      }

      .key {
        color: #444;
        font-weight: bold;
        display: inline;
      }

      .value {
        display: inline;
      }

      &.works .value a {
        @include comma_separated.comma_separated;
        @include link.link;
      }
    }

    .rating {
      bottom: -10px;
      color: #444;
      font-family: Tahoma;
      font-size: 23px;
      line-height: 18px;
      position: absolute;
      right: 0;

      .text {
        color: #555;
        float: left;
        font-size: 11px;
        margin: 5px;
      }
    }

    .best-works {
      overflow: hidden;

      .b-catalog_entry {
        margin-bottom: 0;
      }
    }

    .additional-images {
      margin-left: 10px;
      float: right;

      .link {
        margin-bottom: 15px;

        .title {
          color: #444;
          font-size: 12px;
          font-weight: bold;
          text-align: left;
        }

        img {
          width: 48px;
        }
      }
    }
  }

  .b-add_to_list {
    margin: 10px 0 0 0;
    max-width: 200px;
    min-width: 170px;

    .trigger {
      font-size: 13px;
      line-height: 25px;
    }

    .option {
      font-size: 12px;
    }

    @include responsive.lte_ipad {
      .trigger {
        font-size: 14px;
        line-height: 30px;
      }

      .option {
        font-size: 13px;
      }
    }

    .trigger,
    .add-trigger,
    .remove-trigger {
      max-width: none;
    }
  }

  .rate-edit,
  .rate-show {
    display: none;
  }
}

.b-catalog_entry-tooltip .flexible-button .b-add_to_list {
  padding-bottom: 34px;
  width: auto;
}
